import { NgModule } from '@angular/core';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { UploadModule } from '@progress/kendo-angular-upload'; 
import { DateInputsModule } from '@progress/kendo-angular-dateinputs';

import { SharedModule } from '../shared/shared.module';
import { DiscountRequestComponent } from './components/discount-request.component'; 
import { DiscountRequestService } from './services/discountRequest.service';

import { DiscountRequestRoutingModule } from './discount-routing.module';

@NgModule({
    imports: [
        InputsModule,
        DateInputsModule,
        UploadModule,
        SharedModule.forRoot(),
        DiscountRequestRoutingModule
    ],
    exports:  [ DiscountRequestComponent ],
    declarations: [DiscountRequestComponent ],
    providers: [
        DiscountRequestService
    ]
})
export class DiscountRequestModule { }


