import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';

import { DiscountRequestComponent } from './components/discount-request.component';
import { CurrentUserResolver } from '../shared/services/common/user-resolver.service';

const discountRequestRoutes: Routes = [
    {
        path: ':discountRequestId/:projectId/:quoteId',
        component: DiscountRequestComponent,
        resolve: { user: CurrentUserResolver }
    },
];

@NgModule({
    imports: [RouterModule.forChild(discountRequestRoutes)],
    exports: [RouterModule],
})
export class DiscountRequestRoutingModule {
}


